@import url('https://fonts.googleapis.com/css?family=Roboto:200,500');

body {
  background-color: rgb(244, 254, 255);
}

.header {
  background-color: white;
  font-family: Roboto;
  padding: 1% 10%;
}

h1:first-of-type {
  display: inline-block;
}

.addUser {
  background-color: blue;
  color: white;
  padding: 8px 25px;
  margin-top: 20px;
  float: right;
  cursor: pointer;
  border-radius: 5px;
  border-color: blue;
}

#addIcon {
  margin-right: 10px;
}

.ellipsis {
  position: relative;
  top: 10px;
  background-color: white;
  border: none;
  cursor: pointer;
  color: grey;
  outline: none;
}

.ellipsis:hover {
  color: lightgrey;
}

.edit, .trash {
  text-align: left;
  background-color: white;
  position: relative;
  right: 20px;
  border: none;
  cursor: pointer;
  outline: none;
  width: 100%;
}

.edit:hover>#editIcon {
  color: blue;
}

.trash:hover>#trashIcon {
  color: red;
}

#editIcon, #trashIcon {
  padding-right: 10px;
}

table {
  background-color: white;
  border: 1px solid lightgrey;
  border-collapse: collapse;
  width: 100%;
  z-index: -1;
}

.list {
  margin: 2% 10%;
}

th {
  font-size: 12px;
  font-weight: 200;
  text-align: left;
  padding: 15px;
  color: lightgrey;
}

td {
  padding: 15px;
}

thead>tr:hover {
  border: none;
}

tr:hover {
  border: 2px skyblue solid;
}

.trSelected {
  border: 2px skyblue solid;
}

img {
  position: relative;
  bottom: 5px;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  float: left;
  margin-right: 10px;
}

ul {
  list-style-type: none;
}

li {
  padding: 10px 0px;
}

#optionParent {
  position: relative;
}

.optionModal {
  background-color: white;
  position: absolute;
  top: 70px;
  right: 44px;
  width: 180px;
  border: 1px solid lightgrey;
  border-radius: 5px;
  z-index: 1;
}

.outsideModal {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.89);
}

.insideModal {
  position: fixed;
  left:0;
  right:0;
  top:0;
  bottom:0;
  margin:auto;
  z-index: 9999;
  width: 550px;
  height: 420px;
  background-color: white;
  max-width:100%;
  max-height:100%;
  overflow:auto;
  border-radius: 5px;
  padding: 30px
}

#header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

#header>div:nth-child(1) {
  -ms-flex: 4 1;
      flex: 4 1;
}

#header>div:nth-child(2) {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: right;
  padding-top: 20px;
}

.modalClose {
  background-color: white;
  border: none;
  cursor: pointer;
  color: lightgrey;
}

.modalClose:hover {
  color: black;
}

#section1 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 150px;
  margin-top: 30px;
}

#section1>div:nth-child(1) {
  -ms-flex: 1 1;
      flex: 1 1;
  border: 1px solid lightgrey;
  margin-right: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

#section1>div:nth-child(1)>div:nth-child(1) {
  position: relative;
  -ms-flex: 3 1;
      flex: 3 1;
}

#changePic {
  position: absolute;
  left:0;
  right:0;
  top:0;
  bottom:0;
  margin:auto;
  width: 80px;
  height:80px;
}

#section1>div:nth-child(1)>div:nth-child(2) {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 10px;
}

.uploadBtn {
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

#subSection1 {
  -ms-flex: 2 1;
      flex: 2 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

#subSection1>div:nth-child(1) {
  -ms-flex:1 1;
      flex:1 1;
}

#subSection1>div:nth-child(2) {
  -ms-flex:1 1;
      flex:1 1;
}

#section2 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 70px;
  margin-top: 30px;
}

#section2>div:nth-child(1) {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-right: 10px;
}

#section2>div:nth-child(2) {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-left: 10px;
}

#section3 {
  height: 50px;
  margin-top: 30px;
}

.addUserSubmit {
  width: 100%;
  height: 100%;
  background-color: rgb(4, 110, 208);
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

#username, #address, #contact, #email {
  margin-top: 10px;
  width: 99%;
  height: 30px;
  border-radius: 5px;
}

label, input, button {
  font-family: Roboto;
}



